<template>
  <div class="page-login">
    <div class="login-box form-group">
      <h1>Logo</h1>
      <form-group  size="lg" name="loginform" @on-submit="submit">
        <bo-input
          holder="请输入手机号"
          size="lg"
          icon="people"
          for="loginform"
          theme="underline"
          name="phone"
          format="phone"
          :required="true"
        />
        <div class="p0000">
          <bo-input
            holder="请输入密码"
            type="password"
            size="lg"
            icon="lock"
            for="loginform"
            theme="underline"
            name="password"
            :required="true"
          />
        </div>

      </form-group>
      <div class="p3000">
        <btn theme="primary" :loading="loading" :block=true size="lg" submit="loginform" name="登 录"/>
      </div>
      <div class="flex-box p3000">
        <div class="flex-item-1 tl color-light">
          忘记密码
        </div>
        <div class="flex-item-1 tr color-light">
           还没账号，马上注册
        </div>
      </div>
    </div>
    <toast ref="toast"  :type="type" :content="content"/>
  </div>
</template>

<script>
import { FormGroup, BoInput, Btn, Toast } from 'bone4'//eslint-disable-line

export default {
  name: 'login',
  components: {
    FormGroup,
    BoInput,
    Btn,
    Toast
  },
  props: {

  },
  methods: {
    submit(data) {
      // this.loading = true
      this.$emit('on-login', data)
    },
    reset() {
      this.content = ''
      this.loading = false
    },
    toast(content) {
      this.content = content
      this.$refs.toast.open()
    }
  },
  data() {
    return {
      loading: false,
      content: '',
      type: 'warm'
    }
  }
}
</script>



