<template>
  <div class="layout-bottom bor-top" style="background:#fff">
    <menus-horizon
      size="sm"
      :current="this.$route.path"
      @on-click="handleclick"
      :surface="[{
          icon: 'apps',
          alias: '/templates/home',
          title: '数据沙盘',
          path: '/templates/home'
        },{
          icon: 'safe',
          alias: '/templates/bank',
          title: '数据银行',
          tip: '2',
          path: '/templates/bank'
        },{
          icon: 'people',
          alias: '/templates/user',
          title: '个人中心',
          path: '/templates/user'
        }]"
    ></menus-horizon>
  </div>
</template>

<script>
import { MenusHorizon } from 'bone4'

export default {
  name: 'menudemo',
  components: {
    MenusHorizon
  },
  methods: {
    handleclick(item) {
      this.content = item.alias
    }
  },
  data() {
    return {
      content: 'like'
    }
  }
}
</script>



