import Template from '../pages/templates/index'
import List from '../pages/templates/list'
import Login from '../pages/templates/login'
import Register from '../pages/templates/register'
import UserInfo from '../pages/templates/userinfo'

import Account from '../pages/templates/account'
import SaveInfo from '../pages/templates/data/save-name'
import SavePhone from '../pages/templates/data/save-phone'
import SaveCredit from '../pages/templates/data/save-credit'
import User from '../pages/templates/data/user'
import Home from '../pages/templates/data/home'
import Bank from '../pages/templates/data/bank'
import Data from '../pages/templates/data/data'
import Success from '../pages/templates/data/success'
import Que from '../pages/templates/data/que'
import QueFace from '../pages/templates/data/que-face'

/**
 * 模板页面路由
 */
export default {
  path: '/templates',
  component: Template,
  children: [
    {
      path: 'list',
      component: List
    },
    {
      path: 'login',
      component: Login
    },
    {
      path: 'register',
      component: Register
    },
    {
      path: 'userinfo',
      component: UserInfo
    },
    {
      path: 'account',
      component: Account
    },
    {
      path: 'saveinfo',
      component: SaveInfo
    },
    {
      path: 'savephone',
      component: SavePhone
    },
    {
      path: 'savecredit',
      component: SaveCredit
    },
    {
      path: 'user',
      component: User
    },
    {
      path: 'home',
      component: Home
    },
    {
      path: 'bank',
      component: Bank
    },
    {
      path: 'data',
      component: Data
    },
    {
      path: 'success',
      component: Success
    },
    {
      path: 'que',
      component: Que
    },
    {
      path: 'queface',
      component: QueFace
    }
  ]
}



