<template>
  <div class="">
    <section class="layout-top">
      <header class="header header-opacity">
        <div class="header-dock al">
          <router-link to="/templates/account"><i class="icon iconfont icon-back"></i></router-link>
        </div>
        <div class="header-body">

        </div>
      </header>
    </section>
    <section>
      <div class="account" style="padding:70px 0 50px 0;background:(../../../../static/img/credit.jpg)">
        <p class="account-text">存入数据资产</p>
        <h2 class="account-total" style="font-size: 24px;">
          身份数据
        </h2>
      </div>
      <form-group :theme="theme" size="lg" name="testform" @on-submit="submit">
        <bo-input
          label="身份证号"
          holder="请输入身份证号"
          :required="true"
          for="testform"
          name="text"
        />

        <bo-input
          label="您的姓名"
          holder="请输入姓名"
          :required="true"
          for="testform"
          name="name"
        />

        <div class="p2222">
            <btn theme="primary" :block=true size="lg" submit="testform" name="提交身份认证"/>
        </div>

      </form-group>
    </section>

  </div>
</template>

<script>
import { FormGroup, BoInput, BoSelect, Btn, PhoneCode } from 'bone4'

export default {
  name: 'saveinfo',
  components: {
    FormGroup,
    BoInput,
    BoSelect,
    Btn,
    PhoneCode
  },
  methods: {
    submit(data) {
      console.log(data) // eslint-disable-line
      this.$router.push('/templates/success')
    },
    changeTheme() {
      if (this.theme === 'blank') {
        this.theme = 'head'
      } else {
        this.theme = 'blank'
      }
    }
  },
  data() {
    return {
      theme: 'blank'
    }
  }
}
</script>



