<template>
  <div class="">
    <section class="layout-top">
      <header class="header">
        <div class="header-dock al">
          <router-link to="/demos"><i class="icon iconfont icon-back"></i></router-link>
        </div>
        <div class="header-body">
          注 册
        </div>
      </header>
    </section>
    <section class="layout-has-top">
      <form-group :theme="theme" size="lg" name="testform" @on-submit="submit">
        <phone-code />
        <bo-input
          label="密 码"
          holder="请输入密码"
          :required="true"
          for="testform"
          name="password"
        />

        <div class="p2222">
            <btn theme="primary" :block=true size="lg" submit="testform" name="注 册"/>
        </div>
        <div class="p2222 color-light">
          已有账号，马上登陆
        </div>
      </form-group>
    </section>

  </div>
</template>

<script>
import { FormGroup, BoInput, BoSelect, Btn, PhoneCode } from 'bone4'

export default {
  name: 'register',
  components: {
    FormGroup,
    BoInput,
    BoSelect,
    Btn,
    PhoneCode
  },
  methods: {
    submit(data) {
      console.log(data) // eslint-disable-line
    },
    changeTheme() {
      if (this.theme === 'blank') {
        this.theme = 'head'
      } else {
        this.theme = 'blank'
      }
    }
  },
  data() {
    return {
      theme: 'blank'
    }
  }
}
</script>



